<template>
  <div class="padsize">
      <div class="hardness">
            <el-form ref="padSizeInfo" :model="padSizeInfo" label-width="150px">
              <div class="_title">
                <img src="@/assets/img/h_4.png" alt="">
                <h2 style="padding: 20px 0">坐垫尺寸</h2>
              </div>
              <div class="_box _box_2">
                <template  v-for="item in padSizeInfo.list">
                  <div class="_con" :key="item.id" style="width: 50%; padding-right: 20px; box-sizing: border-box;">
                    <!-- <div @mouseover="padSizeInfoF(item)" @mouseout="padSizeInfoB(item)"> -->
                    <div>
                      <el-form-item :label="item.name" :key="item.id">
                        <el-input v-model="item.val"  @focus="Focus(item)" @blur="blur(item)"></el-input>
                      </el-form-item>
                    </div>
                    <div class="tips" v-if="item.tipsShow">
                      <div class="t_con">
                        <div class="l">
                          <img src="@/assets/img/login_1.png" alt="">
                        </div>
                        <div class="r">
                          <p>{{item.tips}}</p>
                        </div>
                      </div>
                    </div>
                  </div>
                </template>
              <div class="end">
                  <div class="l">
                    <p>+200坐垫侧翼角度</p>
                  </div>
                  <div class="r">
                    <p>41</p>
                  </div>
              </div>
              </div>
            </el-form>
        </div>
  </div>
</template>
 
<script>
export default {
  data () {
    return {
      padSizeInfo: {
        list: [
          {
            name: '+200座垫景中宽度',
            id: '1',
            tips: '以座椅交线为基准，沿躯干线X正向200mm处insert宽度',
            tipsImg: '',
            tipsShow: false,
            err: '',
            errMsg: '',
            val: ''
          },{
            name: '+200座垫宽度',
            id: '2',
            tips: '以座椅交线为基准，沿躯干线X正向200mm处外沿总宽度',
            tipsImg: '',
            tipsShow: false,
            err: '',
            errMsg: '',
            val: ''
          },{
            name: '+200座垫侧翼厚度',
            id: '3',
            tips: '以座椅交线为基准，沿躯干线X正向200mm处侧翼厚度近似值',
            tipsImg: '',
            tipsShow: false,
            err: '',
            errMsg: '',
            val: ''
          },{
            name: '+400座垫景中宽度',
            id: '4',
            tips: '以座椅交线为基准，沿躯干线X正向400mm处insert宽度',
            tipsImg: '',
            tipsShow: false,
            err: '',
            errMsg: '',
            val: ''
          },{
            name: '座垫长度',
            id: '5',
            tips: '以座椅交线为基准，沿躯干线X正向最大长度',
            tipsImg: '',
            tipsShow: false,
            err: '',
            errMsg: '',
            val: ''
          },{
            name: '座垫最大宽度',
            id: '6',
            tips: '沿水平线（Y向）座垫最大宽度',
            tipsImg: '',
            tipsShow: false,
            err: '',
            errMsg: '',
            val: ''
          }
        ],
        result: {
          name: '+400靠背侧翼角度',
          val: ''
        }
      },
    }
  },
  methods: {
        Focus(val) {
            let list = this.padSizeInfo.list;
            list.forEach( item => {
                if(val.id == item.id) {
                    item.tipsShow = true;
                }
            })
            this.padSizeInfo.list = list;
        },
        blur(val) {
            let list = this.padSizeInfo.list;
            list.forEach( item => {
                if(val.id == item.id) {
                    item.tipsShow = false;
                }
            })
            this.padSizeInfo.list = list;
        }
  }
}
</script>
 
<style scoped lang ="stylus">
.padsize
    ._title
        display flex
        align-items center
        img 
            width 20px
            height 20px
            margin-right 6px
        h2
            font-weight 400
            font-size 16px
    ._box
        background #fff
        padding 30px 30px 20px 0
        border-radius 8px
        overflow hidden
        .bw
            .el-form-item
            display inline-block
            width 50%
            .el-select
                width 100%
        .end
            background #E5F0FF
            display flex
            align-items center
            height 40px
            border-radius 8px
            div
                padding-left 20px
    ._box_2
      padding 30px 0 0 0
      background #FFF
      display flex
      flex-wrap wrap
      .con 
        width 50%
        .el-form-item
          width 100%
          margin-bottom 12px
      .tips
        padding-left 40px
        padding-bottom 12px
        .t_con
          display flex
          align-item center
          width 100%
          background #F7F7F7
          border-radius 8px
          padding 12px
          box-sizing border-box
          img
            height 80px
          .r
            display flex
            align-items center
            padding-left 12px
            p 
              font-size 13px
      .end 
        width 100%
</style>