<template>
  <div class="personinfo">
        <div class="person">
            <el-form ref="personInfo" :model="personInfo" label-width="80px">
              <div class="_title">
                <img src="@/assets/img/h_1.png" alt="">
                <h2 style="padding: 20px 0">人员基本信息</h2>
              </div>
            <div class="_box">
                <el-form-item label="姓名">
                  <el-input v-model="personInfo.name"></el-input>
                </el-form-item>

                <div class="bw">
                    <el-form-item label="年龄">
                      <el-select v-model="personInfo.age.val" placeholder="请选择年龄">
                        <el-option :label="item.label" :value="item.val" v-for="item in personInfo.age.list" :key="item.val"></el-option>
                      </el-select>
                    </el-form-item>

                    
                    <el-form-item label="性别">
                      <el-radio-group v-model="personInfo.sex.val">
                        <el-radio :label="item.label" v-for="item in personInfo.sex.list" :key="item.label">{{item.val}}</el-radio>
                      </el-radio-group>
                    </el-form-item>
                </div>

                <div class="bw">
                    <el-form-item label="身高">
                      <el-select v-model="personInfo.height.val_f" placeholder="请选择身高" v-if="personInfo.sex.val == 'f'">
                        <el-option :label="item.label" :value="item.val" v-for="item in personInfo.height.list_f" :key="item.val"></el-option>
                      </el-select>
                      <el-select v-model="personInfo.height.val_m" placeholder="请选择身高" v-if="personInfo.sex.val == 'm'">
                        <el-option :label="item.label" :value="item.val" v-for="item in personInfo.height.list_m" :key="item.val"></el-option>
                      </el-select>
                    </el-form-item>

                    <el-form-item label="体重">
                      <el-select v-model="personInfo.weight.val_f" placeholder="请选择体重" v-if="personInfo.sex.val == 'f'">
                        <el-option :label="item.label" :value="item.val" v-for="item in personInfo.weight.list_f" :key="item.val"></el-option>
                      </el-select>
                      <el-select v-model="personInfo.weight.val_m" placeholder="请选择体重" v-if="personInfo.sex.val == 'm'">
                        <el-option :label="item.label" :value="item.val" v-for="item in personInfo.weight.list_m" :key="item.val"></el-option>
                      </el-select>
                    </el-form-item>
                </div>

                <div class="shape clear-radio__input">
                    <el-form-item label="体型">
                      <el-radio-group v-model="personInfo.shap.val_f" v-if="personInfo.sex.val == 'f'">
                        <el-radio :label="item.label" v-for="item in personInfo.shap.list_f" :key="item.val">
                          <el-image style="width: 45px; height: 45px" :src="item.url" fit="fit"/>
                        </el-radio>
                      </el-radio-group>
                      <el-radio-group v-model="personInfo.shap.val_m" v-if="personInfo.sex.val == 'm'">
                        <el-radio :label="item.label" v-for="item in personInfo.shap.list_m" :key="item.val">
                          <el-image style="width: 45px; height: 45px" :src="item.url" fit="fit"/>
                        </el-radio>
                      </el-radio-group>
                    </el-form-item>
                </div>
            </div>
            </el-form>
        </div>
  </div>
</template>
 
<script>
import { mapState } from "vuex";
export default {
    name: '',
    data () {
        return {
            // 人员信息
            personInfo: {
                name: '',
                sex: {
                    val: 'm',
                    list: [{
                        label: 'm',
                        val: '男'
                    },{
                        label: 'f',
                        val: '女'
                    }]
                },
                age: {
                    val: '',
                    list: [{
                        label: '18~22',
                        val: '1'
                    },{
                        label: '23~27',
                        val: '2'
                    },{
                        label: '28~32',
                        val: '3'
                    },{
                        label: '33~37',
                        val: '4'
                    },{
                        label: '38~42',
                        val: '5'
                    },{
                        label: '43~47',
                        val: '6'
                    },{
                        label: '48~52',
                        val: '7'
                    },{
                        label: '53~57',
                        val: '8'
                    },{
                        label: '58~62',
                        val: '9'
                    },{
                        label: '63+',
                        val: '10'
                    }]
                },
                height: { // 身高
                    val_m: '',
                    val_f: '',
                    list_m: [{
                        label: '160-',
                        val: '1'
                    },{
                        label: '161~165',
                        val: '2'
                    },{
                        label: '166~170',
                        val: '3'
                    },{
                        label: '171~175',
                        val: '4'
                    },{
                        label: '176~180',
                        val: '5'
                    },{
                        label: '181~185',
                        val: '6'
                    },{
                        label: '186+',
                        val: '7'
                    }],
                    list_f: [{
                        label: '150-',
                        val: '1'
                    },{
                        label: '151~155',
                        val: '2'
                    },{
                        label: '156~160',
                        val: '3'
                    },{
                        label: '161~165',
                        val: '4'
                    },{
                        label: '166~170',
                        val: '5'
                    },{
                        label: '171~175',
                        val: '6'
                    },{
                        label: '176+',
                        val: '7'
                    }]
                },
                weight: { // 体重  
                    val_m: '',
                    val_f: '',
                    list_m: [{
                        label: '50-',
                        val: '1'
                    },{
                        label: '51~55',
                        val: '2'
                    },{
                        label: '56~60',
                        val: '3'
                    },{
                        label: '61~65',
                        val: '4'
                    },{
                        label: '66~70',
                        val: '5'
                    },{
                        label: '71~75',
                        val: '6'
                    },{
                        label: '76~80',
                        val: '7'
                    },{
                        label: '81~85',
                        val: '8'
                    },{
                        label: '86~90',
                        val: '9'
                    },{
                        label: '91~95',
                        val: '10'
                    },{
                        label: '95+',
                        val: '11'
                    }],
                    list_f: [{
                        label: '40-',
                        val: '1'
                    },{
                        label: '41~45',
                        val: '2'
                    },{
                        label: '46~50',
                        val: '3'
                    },{
                        label: '51~55',
                        val: '4'
                    },{
                        label: '56~60',
                        val: '5'
                    },{
                        label: '61~65',
                        val: '6'
                    },{
                        label: '66~70',
                        val: '7'
                    },{
                        label: '71~75',
                        val: '8'
                    },{
                        label: '76+',
                        val: '9'
                    }]
                },
                shap: { // 体型
                    val_m: '',
                    val_f: '',
                    list_m: [{
                        label: '1',
                        url: 'https://www.baidu.com/img/bd_logo1.png'
                    },{
                        label: '2',
                        url: 'https://www.baidu.com/img/bd_logo1.png'
                    }],
                    list_f: [{
                        label: '1',
                        url: 'https://www.baidu.com/img/bd_logo1.png'
                    }]
                }
            },
            
        }
    },
    computed: {
        ...mapState({
            user_info: state => state.user_info
        })
    },
    created() {
        console.log( this.user_info )
    },
    watch: {
        // 姓名
        'personInfo.name' (val) {
            console.log( val )
            this.$store.state.user_info.name = val;
        },
        // 年龄
        'personInfo.age.val' (val) {
            console.log( val )
            let list = this.personInfo.age.list;
            list.forEach( item => {
                if(item.val == val) {
                    console.log( item )
                    this.$store.state.user_info.age = {
                        name: item.label,
                        val: val
                    }
                }
            })
        },
        // 性别
        'personInfo.sex.val' (val) {
            console.log( val )
            this.$store.state.user_info.sex = {
                name: val == 'm' ? '男' : '女',
                val: val
            }
        },
        // 身高男
        'personInfo.height.val_m' (val) {
            console.log( val )
            let list = this.personInfo.height.list_m;
            list.forEach( item => {
                if(item.val == val) {
                    console.log( item )
                    this.$store.state.user_info.height.m = {
                        name: item.label,
                        val: val
                    }
                }
            })
        },
        // 身高女
        'personInfo.height.val_f' (val) {
            console.log( val )
            let list = this.personInfo.height.list_f;
            list.forEach( item => {
                if(item.val == val) {
                    console.log( item )
                    this.$store.state.user_info.height.f = {
                        name: item.label,
                        val: val
                    }
                }
            })
        },
        // 体重男
        'personInfo.weight.val_m' (val) {
            console.log( val )
            let list = this.personInfo.weight.list_m;
            list.forEach( item => {
                if(item.val == val) {
                    console.log( item )
                    this.$store.state.user_info.weight.m = {
                        name: item.label,
                        val: val
                    }
                }
            })
        },
        // 体重女
        'personInfo.weight.val_f' (val) {
            console.log( val )
            let list = this.personInfo.weight.list_f;
            list.forEach( item => {
                if(item.val == val) {
                    console.log( item )
                    this.$store.state.user_info.weight.f = {
                        name: item.label,
                        val: val
                    }
                }
            })
        },
        // 体型男
        'personInfo.shap.val_m' (val) {
            console.log( val )
            let list = this.personInfo.shap.list_m;
            list.forEach( item => {
                if(item.label == val) {
                    console.log( item )
                    this.$store.state.user_info.shape.m = {
                        name: item.url,
                        val: val
                    }
                }
            })
        },
        // 体型女
        'personInfo.shap.val_f' (val) {
            console.log( val )
            let list = this.personInfo.shap.list_f;
            list.forEach( item => {
                if(item.label == val) {
                    console.log( item )
                    this.$store.state.user_info.shape.f = {
                        name: item.url,
                        val: val
                    }
                }
            })
        }
    }
}
</script>
<style scoped lang ="stylus">
.personinfo
    ._title
      display flex
      align-items center
      img 
        width 20px
        height 20px
        margin-right 6px
      h2
        font-weight 400
        font-size 16px
    ._box
      background #fff
      padding 30px 30px 20px 0
      border-radius 8px
      overflow hidden
      .bw
        .el-form-item
          display inline-block
          width 50%
          .el-select
            width 100%
      .end
        background #E5F0FF
        display flex
        align-items center
        height 40px
        border-radius 8px
        div
          padding-left 20px







</style>