<template>
  <div class="chooseCar">
      <div class="nav">
          <p v-for="item in nav" :key="item">{{item}}</p>
          <!-- <p v-for="item in listWord" :key="item">{{item}}</p> -->
      </div>
      <div class="list" >
            <div v-for="item in list" :key="item.id">
                <div class="item" :class="item.id == save.id ? 'active' : ''"> 
                    <p style="cursor: pointer" @click="choose(item)"> {{item.name}} </p>
                </div>
            </div>
      </div>
      <div class="list" v-if="list2Show" :style="{height: list.length*26 + 'px'}">
            <div v-for="item in list2" :key="item.id">
                <div class="item" :class="item.id == save2.id ? 'active' : ''"> 
                    <p style="cursor: pointer" @click="choose2(item)"> {{item.name}} </p>
                </div>
            </div>
      </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
    data() {
        return {
            list2Show: false,
            nav: [],
            list: [],
            list2: [],
            save: {
                id: '',
                name: ''
            },
            save2: {
                id: '',
                name: ''
            },
            carBrandList: {}
        }
    },
    props: ["carList"],
    methods: {
        choose(val) {
            console.log( val )
            this.save = val;
            this.$store.state.sit_base_info.type = val;
            if(val.sub) {
                this.list2 = val.sub;
                this.list2Show = true;
            }
            console.log(this.$store.state)
        },
        choose2(val) {
            this.save2 = val;
            this.$store.state.sit_base_info.type = val;
        }
    },
    computed: {
        ...mapState({
            sit_base_info: state => state.sit_base_info
        })
    },
    watch: {
        'carList' (val) {
            console.log(val)
            let obj = val;
            let arr = [];
            let nav = [];
            for (var key in obj) {
                // console.log(obj[key])
                nav.push(key)
                obj[key].forEach(item => {
                    arr.push(item)
                })
            }
            this.nav = nav;
            this.list = arr;
            console.log(arr)
        }
    },
    created() {
        // let that = this;
        // axios.get('/user/userInfo')
        // .then(function(res){
        //     let obj = res.data.data;
        //     let arr = [];
        //     Object.keys(obj).forEach(function(key){
        //         arr.push({
        //             name: key,
        //             list: obj[key]
        //         })
        //     });
        //     that.list = arr;
        // })
        // .catch(function(err){
        //     console.log(err);
        // });

        
        // api.tableInfo().then(res => {
        //   console.log(res)
        //   this.carBrandList = res.data.data.carBrandList;
        //   console.log(this.carBrandList)
        // })
    }
}
</script>
<style lang="stylus" scoped>
.chooseCar
    vertical-align top
    .nav
        background #EEE
        display inline-block
        width 26px
        height 100%
        vertical-align top
        overflow-y scroll
        height 400px
        &::-webkit-scrollbar
            display none
        p 
            width 26px
            text-align center
            line-height 26px
            cursor pointer
    .list
        display inline-block
        overflow-y scroll
        vertical-align top
        border-right 1px solid #EEE
        height 400px
        &::-webkit-scrollbar
            display none
        p
            line-height 26px
            padding 0 10px
            border-bottom 1px solid #EEE
            box-sizing border-box
            width 150px
        .active
            background #3388ff
            color #FFF
</style>