<template>
  <div class="carinfo">
         <div class="chair">
            <el-form ref="chairInfo" :model="chairInfo" label-width="80px">
              <div class="_title">
                <img src="@/assets/img/h_2.png" alt="">
                <h2 style="padding: 20px 0">座椅基本信息</h2>
              </div>
              <div class="_box">
                  <div class="car">
                    <div class="l">车型</div>
                    <div class="r">
                      <el-collapse>
                        <el-collapse-item :title="$store.state.sit_base_info.type.name">
                          <choose-car :carList="carList"></choose-car>
                        </el-collapse-item>
                      </el-collapse>
                    </div>
                  </div>
                  
                <div class="clear-radio__input2">
                  <el-form-item label="级别">
                    <el-radio-group v-model="chairInfo.level.val">
                      <div class="clear-scro_x">
                        <el-radio :label="item.name" v-for="item in chairInfo.level.list" :key="item.id">
                          <el-image style="width: 45px; height: 45px" :src="item.url" fit="fit"/>
                          <p> {{item.name}} </p>
                        </el-radio>
                      </div>
                    </el-radio-group>
                  </el-form-item>
                </div>

                  <el-form-item label="价格">
                    <template>
                      <div style="padding-bottom: 30px">
                        <el-select v-model="chairInfo.price.val" placeholder="请选择价格" style="width: 100%;">
                          <el-option :label="item.name" :value="item.id" v-for="item in chairInfo.price.list" :key="item.id"></el-option>
                        </el-select>
                      </div>
                    </template>
                  </el-form-item>

                <div class="clear-radio__input2">
                  <el-form-item label="颜色">
                    <el-radio-group v-model="chairInfo.color.val">
                      <div class="clear-scro_x">
                        <el-radio :label="item.name" v-for="item in chairInfo.color.list" :key="item.id">
                          <el-image style="width: 45px; height: 45px" :src="item.url" fit="fit"/>
                          <p> {{item.name}} </p>
                        </el-radio>
                      </div>
                    </el-radio-group>
                  </el-form-item>
                </div>

                  <el-form-item label="材质">
                    <el-radio-group v-model="chairInfo.material.val">
                      <el-radio :label="item.name" v-for="item in chairInfo.material.list" :key="item.id"> {{item.name}} </el-radio>
                    </el-radio-group>
                  </el-form-item>
              </div>
            </el-form>
        </div> 
  </div>
</template>
 
<script>
import chooseCar from './chooseCar.vue'
import { mapState } from "vuex";
export default {
  components: {
    chooseCar
  },
  props: ["carList", "levelList", "priceList", "colorList", "materialList"],
  data () {
    return {
      // 座椅基本信息
      chairInfo: {
        car: {
          val: {
            name: '请选择车型'
          },
          list: []
        },
        level: { // 级别
          val: '',
          list: [
            // {
              // name: '新能源',
              // id: '1',
              // url: 'https://www.baidu.com/img/bd_logo1.png'
            // }
          ]
        },
        price: { // 价格
          val: '',
          list: [
            // {
            //   name: '2万',
            //   id: '1'
            // }
          ]
        },
        color: { // 颜色
          val: '',
          list: [
            // {
            //   label: '黑色',
            //   val: '1',
            //   url: 'https://www.baidu.com/img/bd_logo1.png'
            // }
          ]
        },
        material: { // 材质
          val: '',
          list: [
            // {
            //   label: '真皮',
            //   val: '1'
            // }
          ]
        }
      },

    }
  },
  computed: {
      ...mapState({
          sit_base_info: state => state.sit_base_info
      })
  },
  created() {
    // console.log( this.list )
  },
  methods: {
    // 选择车型传参
    carSend(val) {
      console.log(val)
      this.chairInfo.car.val = val;
    },
  },
  watch: {
    'levelList' (val) {
      // console.log(val)
      this.chairInfo.level.list = val;
    },
    'priceList' (val) {
      // console.log(val)
      this.chairInfo.price.list = val;
    },
    'colorList' (val) {
      // console.log(val)
      this.chairInfo.color.list = val;
    },
    'materialList' (val) {
      // console.log(val)
      this.chairInfo.material.list = val;
    },
    // 级别
    'chairInfo.level.val' (val) {
      console.log( val )
      let list = this.chairInfo.level.list;
      list.forEach( item => {
        if(item.label == val) {
          this.$store.state.sit_base_info.level = {
            name: val,
            val: item.val
          }
        }
      })
    },
    // 金额
    'chairInfo.price.val' (val) {
      console.log( val )
      let list = this.chairInfo.price.list;
      list.forEach( item => {
        if(item.val == val) {
          this.$store.state.sit_base_info.price = {
            name: item.label,
            val: val
          }
        }
      })
    },
    // 颜色
    'chairInfo.color.val' (val) {
      console.log( val )
      let list = this.chairInfo.color.list;
      list.forEach( item => {
        if(item.label == val) {
          this.$store.state.sit_base_info.color = {
            name: val,
            val: item.val
          }
        }
      })
    },
    // 材质
    'chairInfo.material.val' (val) {
      console.log( val )
      let list = this.chairInfo.material.list;
      list.forEach( item => {
        if(item.label == val) {
          this.$store.state.sit_base_info.material = {
            name: val,
            val: item.val
          }
        }
      })
    }
  }
}
</script>
 
<style scoped lang ="stylus">
 
.chair
  ._title
      display flex
      align-items center
      img 
          width 20px
          height 20px
          margin-right 6px
      h2
          font-weight 400
          font-size 16px
  ._box
    background #fff
    padding 30px 30px 20px 0
    border-radius 8px
    overflow hidden
    .bw
      .el-form-item
        display inline-block
        width 50%
        .el-select
            width 100%
    .end
        background #E5F0FF
        display flex
        align-items center
        height 40px
        border-radius 8px
        div
            padding-left 20px
  .car
    position relative
    padding-bottom 10px
    .l  
      position absolute
      padding 0 15px 0 40px
      font-size 14px
      top 13px
    .r
      margin-left 80px
      .el-collapse
        border none
        .el-collapse-item__header
          border none
          .el-collapse-item__wrap
            border none

</style>